}
.additionals {
  border: none;
  border-top: 3px solid #68b738;
  margin-bottom: 18px;
}
.additionals__title {
  display: inline-block;
  vertical-align: bottom;
  box-sizing: border-box;
  padding-left: 17px;
  padding-bottom: 7px;
  margin-bottom: 28px;
  width: 182px;
}
.additionals__wrapper {
  width: 280px;
  margin: 0 auto;
}
.additionals__button-wrapper {
  width: 280px;
  margin: 0 auto;
}
.additionals__checkbox {
  display: none;
  margin-right: 16px;
  margin-bottom: 13px;
}
.additionals__checkbox + .additionals__label::before {
  display: inline-block;
  vertical-align: -127%;
  box-sizing: border-box;
  margin-right: 16px;
  margin-bottom: 13px;
  content: "";
  width: 30px;
  height: 30px;
  border: solid 1px #cdcdcd;
  background-color: transparent;
}
.additionals__checkbox:checked + .additionals__label::before {
  background-image: url("../img/icon-tick.svg");
  background-size: 22px 17px;
  background-repeat: no-repeat;
  background-position: center;
}
.additionals__label {
  display: block;
  text-transform: uppercase;
}
