.program-item {
  box-sizing: border-box;
  width: 87.5%;
  padding: 22px 20px;
  background-color: #f2f2f2;
}
.program-item:first-child {
  margin-bottom: 20px;
}
.program-item__description {
  margin: 0;
  padding-bottom: 22px;
  color: #444444;
  font-family: "Arial", sans-serif;
  border-bottom: 1px solid #cdcdcd;
}
.program-item__link {
  display: inline-block;
  margin-top: 19px;
  font-size: 16px;
  line-height: 16px;
  color: #111111;
  text-transform: uppercase;
  vertical-align: middle;
  text-decoration: none;
}
.program-item__link::after {
  display: inline-block;
  vertical-align: baseline;
  width: 25px;
  height: 12px;
  margin-left: 12px;
  content: "";
  background: url("../img/icon-right-arrow-long.svg") no-repeat;
}
.program-item__title {
  margin: 0;
  margin-top: 3px;
  margin-bottom: 20px;
  font-weight: normal;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
}
.program-item__title::after {
  display: inline-block;
  vertical-align: middle;
  content: "";
}
.program-item__title--slim::after {
  width: 35px;
  height: 48px;
  margin-left: -170px;
  margin-top: -10px;
  background-image: url("../img/icon-cat-slim.svg");
  background-repeat: no-repeat;
}
.program-item__title--pro::after {
  width: 67px;
  height: 28px;
  margin-left: -215px;
  margin-top: -4px;
  background-image: url("../img/icon-cat-pro.svg");
  background-repeat: no-repeat;
}
.program-item__title::before {
  display: inline-block;
  vertical-align: middle;
  content: "";
  width: 50px;
  height: 50px;
  margin-right: 20px;
  margin-left: 2px;
  background-color: #68b738;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .program-item {
    min-height: 372px;
    padding-top: 74px;
    padding-left: 52px;
    margin-bottom: 29px;
    width: 92vw;
  }
  .program-item:first-child {
    margin-bottom: 30px;
  }
  .program-item__wrapper {
    display: inline-block;
    width: 600px;
  }
  .program-item__title {
    font-size: 36px;
    margin-bottom: 25px;
  }
  .program-item__description {
    width: 48.4vw;
    margin-bottom: 22px;
    font-size: 16px;
    line-height: 24px;
    border-bottom: none;
  }
  .program-item__description--pro {
    width: 44.8vw;
  }
  .program-item__link {
    margin-top: 10px;
    font-size: 20px;
    line-height: 20px;
  }
  .program-item__link::after {
    width: 25px;
    height: 12px;
    margin-left: 23px;
  }
}
@media (min-width: 768px) and (max-width: 1439px) {
  .program-item {
    position: relative;
  }
  .program-item__title::after {
    position: absolute;
  }
  .program-item__title--slim::after {
    width: 141px;
    height: 194px;
    right: 90px;
    top: 89px;
  }
  .program-item__title--pro::after {
    width: 268px;
    height: 113px;
    right: 27px;
    top: 138px;
  }
  .program-item__title::before {
    position: absolute;
    right: 40px;
    top: 92px;
    width: 200px;
    height: 200px;
  }
}
@media (min-width: 1440px) {
  .program-item {
    box-sizing: border-box;
    width: 570px;
    min-height: 372px;
    padding-top: 46px;
  }
  .program-item:first-child {
    margin-right: 80px;
  }
  .program-item__description {
    width: 462px;
    margin-bottom: 7px;
  }
  .program-item__title {
    margin-bottom: 38px;
  }
  .program-item__title--slim::after {
    width: 71px;
    height: 97px;
    margin-left: -312px;
    margin-top: -19px;
  }
  .program-item__title--pro::after {
    width: 134px;
    height: 57px;
    margin-left: -385px;
    margin-top: -2px;
  }
  .program-item__title::before {
    width: 100px;
    height: 100px;
    margin-right: 60px;
    margin-left: 2px;
  }
  .program-item__link {
    margin-top: 0;
  }
}
.program-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 320px;
  padding: 0;
  margin: 0 auto;
  margin-bottom: 16px;
  list-style-type: none;
}
@media (min-width: 768px) {
  .program-list {
    width: 768px;
  }
}
@media (min-width: 1440px) {
  .program-list {
    width: 1440px;
    flex-direction: row;
    justify-content: center;
    min-height: 372px;
    margin: 0 auto;
    margin-bottom: 42px;
  }
}
.result-item {
  position: relative;
  box-sizing: border-box;
  width: 124px;
  min-height: 56px;
  padding-top: 13px;
  border: 1px solid #cdcdcd;
}
.result-item__number {
  margin: 0;
  font-size: 24px;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;
}
.result-item__description {
  position: absolute;
  width: 59px;
  top: 62%;
  left: 25%;
  font-size: 12px;
  line-height: 12px;
  text-align: center;
  color: #444444;
  font-family: "Arial", sans-serif;
  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .result-item {
    width: 162px;
    min-height: 72px;
  }
  .result-item__number {
    font-size: 30px;
    line-height: 37px;
  }
  .result-item__description {
    width: 127px;
    top: 69%;
    left: 10%;
    font-size: 14px;
    line-height: 14px;
    background-color: #ffffff;
  }
}
@media (min-width: 1440px) {
  .result-item__description {
    background-color: #f2f2f2;
  }
}
.result-section {
  display: flex;
  justify-content: space-between;
  padding: 0;
  margin-bottom: 34px;
  list-style: none;
}
@media (min-width: 768px) {
  .result-section {
    width: 51.5%;
  }
}
@media (min-width: 1440px) {
  .result-section {
    width: 100%;
    justify-content: space-between;
    margin-left: 15px;
    margin-bottom: 44px;
  }
}
