.send-application {
  margin-bottom: 23px;
}
.show-more {
  box-sizing: border-box;
  width: 280px;
  margin: 0 auto;
  border-bottom: 1px solid #cdcdcd;
}
.show-more__title {
  margin: 0;
  margin-top: 5px;
  margin-bottom: 8px;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
}
.show-more__subtitle {
  margin: 0;
  margin-bottom: 24px;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
  text-align: center;
  font-family: "Arial", sans-serif;
}
.show-more__button {
  padding-top: 10px;
  padding-bottom: 9px;
  margin-bottom: 24px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #444444;
  text-transform: uppercase;
  background-color: #f2f2f2;
  border: none;
}
.show-more__link {
  display: block;
  width: 30px;
  margin: 28px auto 18px auto;
}
.show-more__icon {
  fill: #e6e6e6;
  width: 30px;
  height: 30px;
}
@media (min-width: 768px) {
