    margin: 0;
    margin-top: 94px;
    margin-bottom: 49px;
  }
  .show-more__link {
    margin-bottom: 43px;
  }
}
.social-list {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
}
.social-list__item {
  min-height: 20px;
}
.social-list__item:first-child {
  margin-left: -10px;
}
.social-list__item:nth-child(2) {
  margin-left: 36px;
  margin-right: 40px;
}
.social-list__item-image {
  fill: #666666;
}
.social-list__item-image--fb {
  width: 10px;
  height: 19px;
}
.social-list__item-image--insta {
  width: 17px;
  height: 17px;
}
.social-list__item-image--vk {
  width: 22px;
  height: 12px;
}
@media (min-width: 768px) {
  .social-list {
    width: 153px;
  }
  .social-list__item {
    min-height: 28px;
  }
  .social-list__item:first-child {
    margin-top: 6px;
  }
  .social-list__item:nth-child(2) {
    margin-left: 28px;
    margin-right: 29px;
  }
  .social-list__item-image--fb {
    width: 14px;
    height: 28px;
  }
  .social-list__item-image--insta {
    width: 23px;
    height: 23px;
  }
  .social-list__item-image--vk {
    width: 30px;
    height: 16px;
  }
}
